import React from 'react';

import './EmptySearch.css';

const EmptySearch = () => (
  <div className="pt-callout pt-intent-primary pt-icon-info-sign emptysearch">
    <h5>Astuces de recherche</h5>
    <ul className="pt-list">
      <li>Vrifiez que l orthographe des mots-cls est correcte</li>
      <li>Utilisez des mots-cls diffrents</li>
      <li>Supprimez les filtres pour largir votre recherche</li>
    </ul>
  </div>
);

export default EmptySearch;



